











































































































































































import { Component, Vue } from "vue-property-decorator";
@Component({
  components: {}
})
export default class NetworkHealth extends Vue {
  
  chosenSystem: String = "协同办公";
  chosenIndex: number = 3;
  rightTop: any = {};
  rightCenter: any = {};
  rightBottomPanel: number = 0;
  rightBottom: any = {};
  listenScreen() {
    window.addEventListener("resize", () => {
      (this as any).$rem.rem();
    });
  }
  changeIndex(index) {
    this.chosenIndex = index;
  }
  changeIndexAuto(index: number) {
    this.chosenIndex = index;
    clearTimeout((this as any).timerTwo);
    (this as any).timerTwo = setTimeout(() => {
      if (index > 0) {
        this.changeIndexAuto(index - 1);
      } else {
        this.changeIndexAuto(3);
      }
    }, 2000);
  }
  generateNumber(min: number, max: number) {
    return Math.floor(Math.random() * (max - min)) + min;
  }
  generateFloat(min: number, max: number) {
    return Math.random() * max;
  }
  generateSpots() {
    this.rightTop = Array.from({ length: 20 }).map((item, index) => {
      let x = this.generateNumber(0, 100);
      let y = this.generateNumber(0, 100);
      return { name: index, value: [x, y] };
    });
    console.error(JSON.stringify(this.rightTop));
  }
  getDateArray() {}
  exchangeSystem(name: String) {
    this.chosenSystem = name;
    if (this.chosenSystem == "智慧企业") {
      (this.$refs.myCarousel as any).setActiveItem(1);
    }
    if (this.chosenSystem == "协同办公") {
      (this.$refs.myCarousel as any).setActiveItem(0);
    }
    let temp = (this as any).data[name as any];
    let suffixs = [" 个", " 笔", ""];
    ["rightTop", "rightCenter", "rightBottom"].forEach((item, index) => {
      this[item] = temp[item];
      (this.$refs[item] as any).initData(this[item]);
      if (item != "rightBottom") {
        (this as any).$refs[item + "Panel"].changeData(
          0,
          temp[item + "Panel"],
          suffixs[index]
        );
      } else {
        let allData = [];
        temp[item].data.forEach(item => {
          allData = allData.concat(item);
        });
        let average = (
          allData.reduce((a, b) => {
            return a + b;
          }) /
          allData.length /
          1000
        ).toFixed(2);
        (this as any)[item + "Panel"] = average;
      }
    });
  }
  exchangeSystemAuto(index: number) {
    console.error("index-----", index);
    let names: Array<any> = [
      "协同办公",
      "安全邮件",
      "公文管理",
      "信息发布",
      "智慧企业",
      "专有云",
      "智慧党建",
      "大额票据",
      "用户中心"
    ];
    this.exchangeSystem(names[index]);

    clearTimeout((this as any).timer);
    (this as any).timer = setTimeout(() => {
      if (index == names.length - 1) {
        this.exchangeSystemAuto(0);
      } else {
        this.exchangeSystemAuto(index + 1);
      }
    }, 30000);
  }

beforeCreate(){
  this['timer'] = null;
  this['timerTwo'] = null;
this['title'] = "";
  this['subTitle'] = "";
  this['data'] = null;
  this['leftSystemNames'] = [
    "应用服务层",
    "应用系统层",
    "应用平台层",
    "物理平台层"
  ];
  this['lastFloorName'] = [
    "天域服务器 海思版",
    "天域服务器 飞腾版",
    "龙芯服务器",
    "intel服务器"
  ];
  this['firstFloorName'] = [];
  this['firstTwoFloorName'] = [];
  this['secondFloorName'] = [
    { name: "天域分析型数据库", img: "imgdb" },
    { name: "天域关系型数据库", img: "imgdbOne" },
    { name: "天域负载均衡", img: "imgloadBalance" }
  ];
  this['thirdFloorName'] = [
    { name: "天域智能虚拟化平台", img: "imgVirtual" },
    { name: "天域云操作系统", img: "imgOS" },
    { name: "天域分布式存户", img: "imgStorageOne" }
  ];
  this['dayNames'] = [];
}
  mounted() {
    (this as any).$rem.rem();
    (this as any).title = (this as any).$title_2;
    (this as any).subTitle = (this as any).$title_3;
    this.listenScreen();
    setTimeout(() => {
      this.changeIndexAuto(3);
    }, 2000);

    (this as any).data = (this as any).$business;
    (this as any).firstFloorName = (this as any).data.firstFloorName;
    (this as any).firstTwoFloorName = (this as any).data.firstTwoFloorName;
    (this as any).dayNames = (this as any).firstFloorName.concat((this as any).firstTwoFloorName);
    this.exchangeSystemAuto(0);
    this.$nextTick(() => {
      console.error(333,this.$refs.协同办公);
      (this as any).dayNames.forEach((item, index) => {
        (this.$refs[item.name][0] as any).initData((this as any).data[item.name].time);
      });
    });
  }
}
